/* Oculta el botn del men responsive por defecto (solo se muestra cuando la pantalla es pequea) */
a.meanmenu-reveal {
	display: none;
}

/* Contenedor principal del men responsive cuando se activa en pantallas pequeas */
.mean-container .mean-bar {
	float: left;
	width: 100%;
	position: absolute;
	background: transparent;
	padding: 20px 0 0;
	z-index: 999;
	border-bottom: 1px slido con muy baja opacidad;
	height: 55px;
}

/* Estilos del botn hamburguesa que revela el men (en responsive) */
.mean-container a.meanmenu-reveal {
	width: 35px;
	height: 30px;
	padding: 12px 15px 0 0;
	position: absolute;
	right: 0;
	cursor: pointer;
	color: #ffffff;
	text-decoration: none;
	font-size: 1px; /* oculto para lectores, pero presente para accesibilidad */
	text-indent: -9999em; /* oculta el texto */
	line-height: 22px;
	display: block;
	font-weight: 700;
}

/* Lneas del botn hamburguesa (tres rayas blancas) */
.mean-container a.meanmenu-reveal span {
	display: block;
	background: #ffffff;
	height: 4px;
	top: 8px;
	border-radius: 3px;
	position: relative;
	margin-top: -8px;
}

/* Men desplegable del responsive, aparece bajo la barra cuando est activo */
.mean-container .mean-nav {
	float: left;
	width: 100%;
	background: #ffffff;
	margin-top: 55px;
}

/* Lista principal del men (sin estilos por defecto de lista) */
.mean-container .mean-nav ul {
	padding: 0;
	margin: 0;
	width: 100%;
	border: none;
	list-style-type: none;
}

/* Cada tem de la lista ocupa el 100% del ancho y se posiciona relativo */
.mean-container .mean-nav ul li {
	position: relative;
	float: left;
	width: 100%;
}

/* Enlaces del men en mvil (nivel 1) */
.mean-container .mean-nav ul li a {
	display: block;
	float: left;
	width: 90%;
	padding: 1em 5%;
	margin: 0;
	text-align: left;
	color: #677294;
	border-top: 1px slido celeste claro;
	text-decoration: none;
}

/* Enlace activo en men mvil (nivel 1) */
.mean-container .mean-nav ul li a.active {
	color: #000000;
}

/* Enlaces de submens (niveles anidados) */
.mean-container .mean-nav ul li li a {
	width: 80%;
	padding: 1em 10%;
	color: #677294;
	border-top: 1px slido celeste claro;
	opacity: 1;
	filter: alpha(opacity=75);
	text-shadow: none !important;
	visibility: visible;
	text-transform: none;
	font-size: 16px;
}

/* ltimo tem del men, sin borde inferior */
.mean-container .mean-nav ul li.mean-last a {
	border-bottom: none;
	margin-bottom: 0;
}

/* Niveles ms profundos de submen con sangra creciente */
.mean-container .mean-nav ul li li li a {
	width: 70%;
	padding: 1em 15%;
}
.mean-container .mean-nav ul li li li li a {
	width: 60%;
	padding: 1em 20%;
}
.mean-container .mean-nav ul li li li li li a {
	width: 50%;
	padding: 1em 25%;
}

/* Efecto hover en los enlaces del men mvil */
.mean-container .mean-nav ul li a:hover {
	background: rgba(255, 255, 255, 0.1);
}

/* Botn para expandir submens en mvil */
.mean-container .mean-nav ul li a.mean-expand {
	margin-top: -5px;
	width: 100%;
	height: 24px;
	padding: 12px !important;
	text-align: right;
	position: absolute;
	right: 0;
	top: 0;
	z-index: 2;
	font-weight: 700;
	background: transparent;
	border: none !important;
}

/* Resetea flotantes dentro del men */
.mean-container .mean-push {
	float: left;
	width: 100%;
	padding: 0;
	margin: 0;
	clear: both;
}

/* Envoltorio adicional para organizar elementos del men */
.mean-nav .wrapper {
	width: 100%;
	padding: 0;
	margin: 0;
}

/* Corrige problemas de box-sizing en ciertos frameworks como Foundation */
.mean-container .mean-bar, .mean-container .mean-bar * {
	box-sizing: content-box;
}

/* Clase para eliminar componentes del men al volver a escritorio */
.mean-remove {
	display: none !important;
}

/* Oculta el men mvil en vista escritorio */
.mobile-nav {
	display: none;
}

/* Color del enlace activo en men mvil */
.mobile-nav.mean-container .mean-nav ul li a.active {
	color: #ff6464;
}

/* Men principal (vista escritorio) */
.main-nav {
	background: #000;
	position: inherit;
	top: 0;
	left: 0;
	padding-top: 15px;
	padding-bottom: 15px;
	width: 100%;
	z-index: 999;
	height: auto;
}

/* Oculta la flecha del dropdown para no mostrarla */
.mean-nav .dropdown-toggle::after {
	display: none;
}

/* Lista del men principal, sin estilos por defecto */
.main-nav nav ul {
	padding: 0;
	margin: 0;
	list-style-type: none;
}

/* tems del men principal (espaciado interno) */
.main-nav nav .navbar-nav .nav-item {
	position: relative;
	padding: 15px 0;
}

/* Enlaces del men principal */
.main-nav nav .navbar-nav .nav-item a {
	font-weight: 500;
	font-size: 16px;
	text-transform: capitalize;
	color: #ffffff;
	padding: 0;
	margin: 0 15px;
}

/* Colores al pasar el mouse o en estado activo */
.main-nav nav .navbar-nav .nav-item a:hover,
.main-nav nav .navbar-nav .nav-item a:focus,
.main-nav nav .navbar-nav .nav-item a.active {
	color: #ff6464;
}

/* Color para tem al estar sobre l */
.main-nav nav .navbar-nav .nav-item:hover a {
	color: #ff6464;
}

/* Estilos para los submens desplegables */
.main-nav nav .navbar-nav .nav-item .dropdown-menu {
	box-shadow: 0 0 30px rgba(0, 0, 0, 0.05);
	background: #0d1028;
	position: absolute;
	top: 80px;
	left: 0;
	width: 250px;
	z-index: 99;
	display: block;
	padding: 20px 5px;
	opacity: 0;
	visibility: hidden;
	transition: all 0.3s ease-in-out;
}

/* Estilos individuales para tems del submen */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li {
	position: relative;
	padding: 0;
}

/* Enlaces dentro del submen */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li a {
	font-size: 15px;
	font-weight: 500;
	text-transform: capitalize;
	padding: 9px 15px;
	margin: 0;
	display: block;
	color: #ffffff;
}

/* Enlace activo o con hover dentro del submen */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li a:hover,
.main-nav nav .navbar-nav .nav-item .dropdown-menu li a:focus,
.main-nav nav .navbar-nav .nav-item .dropdown-menu li a.active {
	color: #ff6464;
}

/* Submens anidados (nivel 2 y ms) ocultos inicialmente */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li .dropdown-menu {
	position: absolute;
	left: -100%;
	top: 0;
	opacity: 0 !important;
	visibility: hidden !important;
}

/* Muestra submens anidados al pasar el mouse */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li:hover .dropdown-menu {
	opacity: 1 !important;
	visibility: visible !important;
	top: -20px !important;
}

/* Submen de nivel 3 dentro de otro anidado */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li .dropdown-menu li .dropdown-menu {
	position: absolute;
	left: -100%;
	top: 0;
	opacity: 0 !important;
	visibility: hidden !important;
}

/* Muestra tercer nivel al hacer hover en segundo */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li:hover .dropdown-menu li:hover .dropdown-menu {
	opacity: 1 !important;
	visibility: visible !important;
	top: -20px !important;
}

/* Estilos para enlaces del tercer nivel */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li .dropdown-menu li a {
	color: #ffffff;
	text-transform: capitalize;
}

/* Hover para enlaces del tercer nivel */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li .dropdown-menu li a:hover,
.main-nav nav .navbar-nav .nav-item .dropdown-menu li .dropdown-menu li a:focus,
.main-nav nav .navbar-nav .nav-item .dropdown-menu li .dropdown-menu li a.active {
	color: #ff6464;
}

/* Hover general para tems del submen */
.main-nav nav .navbar-nav .nav-item .dropdown-menu li:hover a {
	color: #ff6464;
}

/* Mostrar submen al pasar el mouse sobre el tem padre */
.main-nav nav .navbar-nav .nav-item:hover ul {
	opacity: 1;
	visibility: visible;
	top: 100%;
}

/* Ajuste para que el ltimo tem no se desborde a la derecha */
.main-nav nav .navbar-nav .nav-item:last-child .dropdown-menu {
	left: auto;
	right: 0;
}
